/* helpers */

.flex {
  display: flex
}

.justify-start {
  justify-content:start
}

.justify-center {
  justify-content: center
}

.align-start {
  align-items: start
}

.align-center {
  align-items: center
}



/* header */

.App-header {
  background-color:rgb(123, 149, 163);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: sticky;
  top: 0;
  z-index: 10;
  border: 3px solid white;
}

.App-header span {
  font-size: 1.5rem;
  text-shadow: 2px 2px 5px black;
  color: white
}

/* Personal Details */

.details-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 1rem;
}

.details-container {
  display: flex;
  align-items: center;
  margin-bottom: 1rem
}

.card-container {
  margin: 0 0 0 2rem;
  box-shadow: 3px 4px 5px 4px #888888
}

@media (max-width: 600px) {
  .details-wrapper {
    justify-content: center;
    align-items: center
  }
  .details-container{
    flex-direction:column
  }
  .card-container {
    margin: 1rem 0 0 0
  }
}

/* Accordion */

.accordion-tab-container{
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.accordion-info-container {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
  box-shadow: 3px 4px 5px 4px #888888;
}

.accordion-info-container a span{
  font-weight: bold;
  text-decoration: underline;
}

@media (max-width: 320px) {
  .accordion-tab-container{
   flex-direction:column
  }
  .accordion-info-container{
   margin: 1rem 0 0 0;
  }
}

/* PrimeReact components fixups*/

.p-panel-content{
  border-color: transparent !important;
  font-size:1.5rem;
}

@media (max-width: 600px) {
  .p-panel-content{
    font-size:1rem;
  }
}
.p-accordion-content{
  padding-left: 1px !important;
  padding-bottom: 10px !important;
}

.p-accordion-content .rotating-cube{
  height: 10rem;
  width: 10rem;
  min-width: 9rem;
  min-height: 9rem;
  background-color: rgb(123, 149, 163);
  box-shadow: 3px 4px 5px 4px #888888;
}

.p-tabview{
  padding-top: 5px !important;
  padding-left: 5px !important;
  padding-right: 5px !important;
}

.p-tabview-nav{
   padding-left: 5px !important;
   padding-right: 5px !important;
}

.p-tabview-panels{
  padding-left: 5px !important;
  padding-right: 5px !important;
}
